.contacts__items-wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 570px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.contacts__text {
  font-family: $main-font-family;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: $main-header-text-color;
  padding-bottom: 30px;
  margin: 0;
}

.contacts__item {
  font-family: $main-font-family;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: $main-header-text-color;
  padding: 0;
  margin: 0;
}

@media (min-width: $desktop-width) {
  .contacts__item:nth-child(2) {
    order: 2;
  }
}

.contacts__phone,
.contacts__mail,
.contacts__addres {
  display: block;
  font-family: $main-font-family;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  color: #d22856;
  padding: 0;
  margin: 0 auto;
}
